import {inject} from 'aurelia-framework';
import {Search} from './search';
import {Router} from 'aurelia-router';

@inject(Router)

export class SearchList {
  heading = "Searches";
  searchQuery = '';
  searches = [];
  currentSearch = undefined;

  constructor(router){
    this.router = router;
  }

  addSearch() {
    // if (this.currentSearch) {
    //   this.saveVotes(this.currentSearch);
    // }
    var currentQuery = this.searchQuery
    if (currentQuery) {
      // TODO: ensure uniqueness of queries
      // this.router.navModel.setTitle('Search: ' + params.query);
      this.currentSearch = new Search(currentQuery);
      this.searches.push(this.currentSearch);

      this.searchQuery = '';
      this.router.navigate('designs/'+currentQuery);
      // this.routeConfig.navModel.setTitle('Search: ' + params.query);
    }
  }

  // saveVotes(search) {
  //
  // }
}
